<template>
    <div class="container">
        <EventStory/>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, watch } from 'vue'
import { useLanguage } from '@/store/Language'
import { storeToRefs } from 'pinia'
import EventStory from '@/components/EventStory.vue'

export default {
    name: 'StoryView',
    components: {
        EventStory,
    },
    setup() {
        const { language } = storeToRefs(useLanguage())
        useLanguage().changeLanguage()
        watch(language, () => {
            useLanguage().changeLanguage()
        })
    }
}
</script>

